/* ========== 기본 레이아웃 ========== */
.c-store-03 {
    padding: 24px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }
  
  .page-header h1 {
    font-size: 24px;
    font-weight: 700;
    color: #212529;
  }
  
  .btn-save {
    padding: 10px 24px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .btn-save:hover:not(:disabled) {
    background-color: #3942d9;
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.2);
  }
  
  .btn-save:disabled {
    background-color: #e9ecef;
    color: #6c757d;
    cursor: not-allowed;
  }
  
  /* ========== 콘텐츠 영역 ========== */
  .content-container {
    background: white;
    border-radius: 12px;
    padding: 32px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
  }
  
  /* ========== 섹션 스타일 ========== */
  .section {
    margin-bottom: 40px;
  }
  
  .section:last-child {
    margin-bottom: 0;
  }
  
  .section-title {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin-bottom: 8px;
  }
  
  .section-description {
    font-size: 14px;
    color: #6c757d;
    margin-bottom: 20px;
  }
  
  .section-divider {
    margin: 32px 0;
    border: 0;
    border-top: 1px solid #e9ecef;
  }
  
  /* ========== 검사 대행 설정 ========== */
  .capacity-setting {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 16px;
  }
  
  .capacity-setting label {
    font-weight: 500;
    color: #212529;
  }
  
  .capacity-input {
    width: 80px;
    padding: 8px 12px;
    border: 1px solid #dee2e6;
    border-radius: 6px;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
  }
  
  .capacity-input:focus {
    outline: none;
    border-color: #4852EA;
  }
  
  .capacity-unit {
    color: #495057;
    font-weight: 500;
  }
  
  /* ========== 지역 선택 ========== */
  .region-selector {
    display: flex;
    gap: 16px;
    margin-top: 20px;
  }
  
  .region-column {
    flex: 1;
    min-height: 400px;
    max-height: 500px; /* 최대 높이 설정 */
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  
  .region-column-header {
    padding: 12px 16px;
    background-color: white;
    border-bottom: 1px solid #dee2e6;
    font-weight: 600;
    color: #212529;
    flex-shrink: 0; /* 헤더는 고정 */
  }
  
  .region-list {
    flex: 1;
    overflow-y: auto; /* 세로 스크롤 활성화 */
    overflow-x: hidden; /* 가로 스크롤 비활성화 */
    padding: 8px;
  }
  
  /* 스크롤바 스타일링 */
  .region-list::-webkit-scrollbar {
    width: 8px;
  }
  
  .region-list::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 4px;
  }
  
  .region-list::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 4px;
  }
  
  .region-list::-webkit-scrollbar-thumb:hover {
    background: #a8a8a8;
  }
  
  /* Firefox 스크롤바 스타일링 */
  .region-list {
    scrollbar-width: thin;
    scrollbar-color: #c1c1c1 #f1f1f1;
  }
  
  .region-item {
    display: flex;
    align-items: center;
    padding: 10px 12px;
    margin-bottom: 4px;
    background-color: white;
    border: 1px solid #e9ecef;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .region-item:hover {
    background-color: #f8f9fa;
    border-color: #adb5bd;
  }
  
  .region-item.selected {
    background-color: #e7e9fb;
    border-color: #4852EA;
  }
  
  .region-item input[type="checkbox"] {
    margin-right: 8px;
    flex-shrink: 0; /* 체크박스 크기 고정 */
  }
  
  .region-item label {
    flex: 1;
    cursor: pointer;
    margin: 0;
    font-size: 14px;
    word-break: keep-all; /* 한글 단어 단위로 줄바꿈 */
  }
  
  /* 선택된 지역 표시 영역 */
  .selected-regions-column {
    background-color: #f0f2ff;
  }
  
  .selected-regions-column .region-column-header {
    background-color: #e7e9fb;
    color: #4852EA;
  }
  
  .selected-region-tag {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 12px;
    margin-bottom: 4px;
    background-color: white;
    border: 1px solid #4852EA;
    border-radius: 6px;
    font-size: 14px;
    color: #212529;
  }
  
  .selected-region-tag button {
    background: none;
    border: none;
    color: #6c757d;
    cursor: pointer;
    padding: 0;
    margin-left: 8px;
    font-size: 18px;
    line-height: 1;
    transition: color 0.2s;
    flex-shrink: 0;
  }
  
  .selected-region-tag button:hover {
    color: #dc3545;
  }
  
  /* ========== 빈 상태 메시지 ========== */
  .empty-message {
    text-align: center;
    padding: 40px 20px;
    color: #adb5bd;
    font-size: 14px;
  }
  
  /* ========== 로딩 상태 ========== */
  .loading-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    color: #6c757d;
  }
  
  /* ========== 반응형 ========== */
  @media (max-width: 768px) {
    .c-store-03 {
      padding: 16px;
    }
    
    .content-container {
      padding: 20px;
    }
    
    .region-selector {
      flex-direction: column;
    }
    
    .region-column {
      min-height: 300px;
      max-height: 350px; /* 모바일에서는 높이를 더 작게 */
    }
  }