.date-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.date-modal {
  background: white;
  border-radius: 16px;
  width: 350px;
  max-width: 90%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.date-modal-header {
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
  text-align: center;
}

.date-modal-header h3 {
  font-size: 16px;
  font-weight: 600;
  color: #212529;
  margin: 0;
}

.date-modal-body {
  padding: 20px;
}

/* 월 선택기 */
.month-selector {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.month-selector button {
  background: none;
  border: none;
  font-size: 18px;
  color: #4852EA;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: all 0.2s;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.month-selector button:hover {
  background-color: #f0f2ff;
}

.month-selector span {
  font-size: 16px;
  font-weight: 600;
  color: #212529;
}

/* 요일 헤더 */
.weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin-bottom: 8px;
}

.weekdays span {
  text-align: center;
  font-size: 12px;
  color: #6c757d;
  font-weight: 500;
  padding: 8px 0;
}

/* 날짜 그리드 */
.days-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
}

.day-button {
  aspect-ratio: 1;
  background-color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  color: #495057;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}

.day-button:hover:not(.empty) {
  background-color: #f0f2ff;
  color: #4852EA;
}

.day-button.selected {
  background-color: #4852EA;
  color: white;
  font-weight: 600;
}

.day-button.empty {
  visibility: hidden;
  cursor: default;
}

/* 년도 선택기 */
.year-selector {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-bottom: 24px;
}

.year-selector button {
  background: none;
  border: none;
  font-size: 20px;
  color: #4852EA;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 4px;
  transition: all 0.2s;
}

.year-selector button:hover {
  background-color: #f0f2ff;
}

.year-selector span {
  font-size: 18px;
  font-weight: 600;
  color: #212529;
  min-width: 80px;
  text-align: center;
}

/* 월 그리드 */
.month-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 8px;
}

.month-button {
  padding: 16px;
  background-color: white;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  font-size: 14px;
  color: #495057;
  cursor: pointer;
  transition: all 0.2s;
}

.month-button:hover {
  border-color: #4852EA;
  color: #4852EA;
  background-color: #f0f2ff;
}

.month-button.selected {
  background-color: #4852EA;
  border-color: #4852EA;
  color: white;
  font-weight: 600;
}

/* 반응형 */
@media (max-width: 480px) {
  .date-modal {
    width: 100%;
    margin: 16px;
  }
  
  .month-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}