.phone-number-input {
    width: 100%;
    padding: 12px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 16px;
    transition: all 0.2s;
  }
  
  .phone-number-input:focus {
    outline: none;
    border-color: #4852EA;
    box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
  }