/* 사진 모달 오버레이 */
.photo-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
  }
  
  /* 모달 컨테이너 */
  .photo-modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 90vw;
    max-height: 90vh;
    padding: 60px 100px 20px; /* 좌우 패딩 추가 */
  }
  
  /* 헤더 영역 */
  .modal-header-area {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
    height: 50px;
  }
  
  /* 사진 카운터 */
  .modal-photo-counter {
    color: rgba(255, 255, 255, 0.9);
    font-size: 14px;
    min-width: 50px;
  }
  
  /* 사진 이름 */
  .modal-photo-name {
    color: white;
    font-size: 16px;
    text-align: center;
    flex: 1;
    padding: 0 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  /* 닫기 버튼 */
  .modal-close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 28px;
    cursor: pointer;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.2s;
    padding: 0;
    line-height: 1;
  }
  
  .modal-close-btn:hover {
    opacity: 0.7;
  }
  
  /* 사진 래퍼 */
  .modal-photo-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
    position: relative;
  }
  
  /* 사진 */
  .modal-photo {
    max-width: 100%;
    max-height: 60vh;
    object-fit: contain;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  }
  
  /* 대표 사진 뱃지 */
  .modal-main-badge {
    background-color: #28a745;
    color: white;
    padding: 10px 24px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    display: inline-flex;
    align-items: center;
    gap: 6px;
  }
  
  /* 대표 사진 설정 버튼 */
  .modal-set-main-btn {
    padding: 10px 24px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .modal-set-main-btn:hover {
    background-color: #3942d9;
    box-shadow: 0 2px 8px rgba(72, 82, 234, 0.3);
  }
  
  /* 네비게이션 버튼 - 사진 바깥으로 이동 */
  .modal-nav-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.4);
    color: white;
    font-size: 36px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
    z-index: 10;
  }
  
  .modal-nav-btn:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-50%) scale(1.1);
  }
  
  .modal-nav-btn.prev {
    left: -70px; /* 사진 바깥쪽으로 위치 */
  }
  
  .modal-nav-btn.next {
    right: -70px; /* 사진 바깥쪽으로 위치 */
  }
  
  /* 모바일 대응 */
  @media (max-width: 768px) {
    .modal-content {
      padding: 60px 20px 20px; /* 모바일에서는 패딩 줄임 */
    }
    
    .modal-header-area {
      padding: 0 10px;
    }
    
    .modal-photo-name {
      font-size: 14px;
    }
    
    .modal-nav-btn {
      width: 40px;
      height: 40px;
      font-size: 24px;
    }
    
    /* 모바일에서는 버튼을 화면 가장자리에 */
    .modal-nav-btn.prev {
      left: 10px;
    }
    
    .modal-nav-btn.next {
      right: 10px;
    }
  }
  
  /* 태블릿 대응 */
  @media (min-width: 769px) and (max-width: 1024px) {
    .modal-nav-btn.prev {
      left: -50px;
    }
    
    .modal-nav-btn.next {
      right: -50px;
    }
  }