.vehicle-info-modal {
    max-width: 600px;
  }
  
  .vehicle-type-display {
    margin-top: 20px;
    padding: 16px;
    background: #f8f9fa;
    border-radius: 8px;
    border: 1px solid #e9ecef;
  }
  
  .vehicle-type-label {
    font-size: 14px;
    color: #6c757d;
    margin-bottom: 8px;
  }
  
  .vehicle-type-value {
    font-size: 16px;
    font-weight: 600;
    color: #212529;
  }
  
  .vehicle-type-note {
    font-size: 13px;
    color: #6c757d;
    margin-top: 4px;
  }