.c-res-01-02 {
    padding: 0;
  }
  
  /* 페이지 헤더 */
  .page-header {
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .page-header h1 {
    font-size: 24px;
    font-weight: 700;
    color: #212529;
    margin: 0;
  }
  
  .new-indicator {
    display: inline-block;
    padding: 4px 8px;
    background-color: #dc3545;
    color: white;
    font-size: 12px;
    font-weight: 700;
    border-radius: 4px;
    animation: pulse 2s infinite;
  }
  
  @keyframes pulse {
    0% {
      opacity: 1;
      transform: scale(1);
    }
    50% {
      opacity: 0.8;
      transform: scale(1.05);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  /* 검색 필터 */
  .search-filters {
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    margin-bottom: 20px;
  }
  
  .filter-row {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  
  .filter-group select {
    padding: 10px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 14px;
    background: white;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .filter-group select:hover {
    border-color: #4852EA;
  }
  
  .filter-group select:focus {
    outline: none;
    border-color: #4852EA;
    box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
  }
  
  .date-range {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .date-button {
    padding: 10px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    background: white;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .date-button:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .date-separator {
    color: #6c757d;
  }
  
  .search-input-group {
    display: flex;
    gap: 8px;
    flex: 1;
  }
  
  .search-input {
    flex: 1;
    padding: 10px 16px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 14px;
    transition: all 0.2s;
  }
  
  .search-input:focus {
    outline: none;
    border-color: #4852EA;
    box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
  }
  
  .search-button {
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
    background: #4852EA;
    color: white;
    border: none;
  }
  
  .search-button:hover {
    background: #3943d0;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  }
  
  /* 대행 요청 알림 */
  .delegation-alert {
    background: #fff3cd;
    border: 1px solid #ffeaa7;
    border-radius: 8px;
    padding: 16px 20px;
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
    animation: slideIn 0.3s ease-out;
  }
  
  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .alert-icon {
    font-size: 20px;
    animation: ring 1s ease-in-out infinite;
  }
  
  @keyframes ring {
    0%, 100% {
      transform: rotate(0deg);
    }
    10%, 30% {
      transform: rotate(-10deg);
    }
    20%, 40% {
      transform: rotate(10deg);
    }
  }
  
  .alert-text {
    flex: 1;
    color: #856404;
    font-weight: 500;
    font-size: 15px;
  }
  
  .alert-action {
    padding: 8px 16px;
    background: #ffc107;
    color: #212529;
    border: none;
    border-radius: 6px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .alert-action:hover {
    background: #e0a800;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(255, 193, 7, 0.3);
  }
  
  /* 대행 테이블 */
  .delegation-table {
    background: white;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  }
  
  .delegation-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .delegation-table th {
    background: #f8f9fa;
    padding: 16px;
    text-align: left;
    font-weight: 600;
    font-size: 14px;
    color: #495057;
    border-bottom: 1px solid #dee2e6;
    white-space: nowrap;
  }
  
  .delegation-table td {
    padding: 16px;
    font-size: 14px;
    color: #212529;
    border-bottom: 1px solid #f1f3f5;
  }
  
  .delegation-table tr:hover {
    background: #f8f9fa;
  }
  
  .delegation-table tr:last-child td {
    border-bottom: none;
  }
  
  /* 상태 배지 */
  .status-badge {
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    gap: 4px;
  }
  
  /* 액션 버튼 */
  .action-buttons {
    display: flex;
    gap: 8px;
  }
  
  .action-btn {
    padding: 6px 12px;
    border-radius: 6px;
    font-size: 13px;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
    white-space: nowrap;
  }
  
  .action-btn.confirm {
    background: #28a745;
    color: white;
  }
  
  .action-btn.confirm:hover {
    background: #218838;
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(40, 167, 69, 0.3);
  }
  
  .action-btn.reject {
    background: #6c757d;
    color: white;
  }
  
  .action-btn.reject:hover {
    background: #5a6268;
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(108, 117, 125, 0.3);
  }
  
  .action-btn.complete {
    background: #007bff;
    color: white;
  }
  
  .action-btn.complete:hover {
    background: #0069d9;
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
  
  .action-btn.cancel {
    background: #dc3545;
    color: white;
  }
  
  .action-btn.cancel:hover {
    background: #c82333;
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(220, 53, 69, 0.3);
  }
  
  .completed-text {
    color: #6c757d;
    font-size: 13px;
  }
  
  /* 페이지네이션 */
  .pagination {
    display: flex;
    justify-content: center;
    gap: 8px;
    margin-top: 24px;
  }
  
  .page-btn {
    padding: 8px 12px;
    border: 1px solid #dee2e6;
    border-radius: 6px;
    background: white;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
    min-width: 36px;
    font-size: 14px;
  }
  
  .page-btn:hover {
    border-color: #4852EA;
    color: #4852EA;
  }
  
  .page-btn.active {
    background: #4852EA;
    color: white;
    border-color: #4852EA;
  }
  
  .page-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  /* 모달 스타일 */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.2s ease-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .modal-content {
    background: white;
    border-radius: 12px;
    width: 500px;
    max-width: 90%;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
    animation: modalSlideIn 0.3s ease-out;
  }
  
  @keyframes modalSlideIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .modal-header h2 {
    font-size: 20px;
    font-weight: 700;
    margin: 0;
  }
  
  .modal-close {
    background: none;
    border: none;
    font-size: 28px;
    color: #6c757d;
    cursor: pointer;
    padding: 0;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    transition: all 0.2s;
  }
  
  .modal-close:hover {
    background: #f8f9fa;
    color: #212529;
  }
  
  /* 반응형 */
  @media (max-width: 1200px) {
    .filter-row {
      flex-wrap: wrap;
    }
    
    .search-input-group {
      width: 100%;
      margin-top: 8px;
    }
  }
  
  @media (max-width: 768px) {
    .delegation-table {
      overflow-x: auto;
    }
    
    .delegation-table table {
      min-width: 700px;
    }
    
    .page-header {
      flex-wrap: wrap;
    }
    
    .delegation-alert {
      flex-direction: column;
      text-align: center;
    }
    
    .alert-action {
      width: 100%;
    }
  }
  
  /* 로딩 상태 */
  .loading-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
  }
  
  .spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #f3f3f3;
    border-top: 3px solid #4852EA;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  /* 빈 상태 */
  .empty-state {
    text-align: center;
    padding: 60px 20px;
    color: #6c757d;
  }
  
  .empty-state-icon {
    font-size: 48px;
    margin-bottom: 16px;
    opacity: 0.5;
  }
  
  .empty-state-text {
    font-size: 16px;
    margin-bottom: 8px;
  }
  
  .empty-state-subtext {
    font-size: 14px;
    color: #adb5bd;
  }