/* ========== 기본 레이아웃 ========== */
.c-store-02 {
    padding: 24px;
    max-width: 1200px;
    margin: 0 auto;
}

.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
}

.page-header h1 {
    font-size: 24px;
    font-weight: 700;
    color: #212529;
}

.page-description {
    font-size: 14px;
    color: #6c757d;
    margin-bottom: 24px;
}

.btn-save {
    padding: 10px 24px;
    background-color: #4852EA;
    color: white;
    border: none;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-save:hover:not(:disabled) {
    background-color: #3942d9;
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.2);
}

.btn-save:disabled {
    background-color: #e9ecef;
    color: #6c757d;
    cursor: not-allowed;
}

/* ========== 검사소 전용 스타일 ========== */
.inspection-content {
    background: white;
    border-radius: 12px;
    padding: 32px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
}

.section {
    margin-bottom: 40px;
}

.section:last-child {
    margin-bottom: 0;
}

.section-title {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin-bottom: 8px;
}

.section-description {
    font-size: 13px;
    color: #6c757d;
    margin-bottom: 20px;
}

.section-divider {
    margin: 32px 0;
    border: 0;
    border-top: 1px solid #e9ecef;
}

/* ========== 검사 비용 테이블 ========== */
.price-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 16px;
}

.price-table th,
.price-table td {
    padding: 12px;
    text-align: center;
    border: 1px solid #dee2e6;
}

.price-table th {
    background-color: #f8f9fa;
    font-weight: 600;
    font-size: 14px;
    color: #212529;
}

.price-table td:first-child {
    text-align: left;
    font-weight: 500;
    background-color: #f8f9fa;
}

.price-table input[type="number"] {
    width: 80px;
    padding: 6px 8px;
    border: 1px solid #dee2e6;
    border-radius: 4px;
    text-align: right;
    font-size: 14px;
}

.price-table input[type="number"]:focus {
    outline: none;
    border-color: #4852EA;
}

.price-table input[type="checkbox"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
}

.price-unit {
    font-size: 12px;
    color: #6c757d;
    text-align: right;
    margin-top: 4px;
}

/* ========== 추가 검사 항목 ========== */
.additional-items {
    margin-top: 20px;
}

.additional-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 12px 16px;
    background-color: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    margin-bottom: 12px;
}

.additional-item-name {
    flex: 1;
    font-weight: 500;
    color: #212529;
    display: flex;
    align-items: center;
    gap: 8px;
}

.help-icon {
    color: #6c757d;
    cursor: help;
    font-size: 14px;
}

.additional-item input[type="checkbox"] {
    margin-right: 8px;
}

.additional-item input[type="number"] {
    width: 100px;
    padding: 6px 8px;
    border: 1px solid #dee2e6;
    border-radius: 4px;
    text-align: right;
}

/* ========== 시간당 검사 가능 차량 수 ========== */
.capacity-setting {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 16px;
}

.capacity-setting label {
    font-weight: 500;
    color: #212529;
    display: flex;
    align-items: center;
    gap: 8px;
}

.capacity-input {
    width: 80px;
    padding: 8px 12px;
    border: 1px solid #dee2e6;
    border-radius: 6px;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
}

.capacity-input:focus {
    outline: none;
    border-color: #4852EA;
}

.capacity-unit {
    color: #495057;
    font-weight: 500;
}

/* ========== 카센터 전용 스타일 ========== */
.carcenter-content {
    display: flex;
    gap: 24px;
}

.service-sidebar {
    width: 200px;
    background: white;
    border-radius: 12px;
    padding: 16px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
    height: fit-content;
    position: sticky;
    top: 24px;
}

.service-menu-item {
    display: block;
    width: 100%;
    padding: 12px 16px;
    margin-bottom: 4px;
    background: none;
    border: none;
    border-radius: 8px;
    text-align: left;
    font-size: 14px;
    font-weight: 500;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
}

.service-menu-item:hover {
    background-color: #f8f9fa;
    color: #212529;
}

.service-menu-item.active {
    background-color: #e7e9fb;
    color: #4852EA;
    font-weight: 600;
}

.service-submenu {
    margin-left: 16px;
    margin-top: 4px;
    margin-bottom: 8px;
}

.service-submenu-item {
    display: block;
    width: calc(100% - 16px);
    padding: 8px 12px;
    margin-bottom: 2px;
    background: none;
    border: none;
    border-radius: 6px;
    text-align: left;
    font-size: 13px;
    color: #6c757d;
    cursor: pointer;
    transition: all 0.2s;
}

.service-submenu-item:hover {
    background-color: #f8f9fa;
    color: #495057;
}

.service-submenu-item.active {
    background-color: #f0f2ff;
    color: #4852EA;
    font-weight: 500;
}

.service-main-content {
    flex: 1;
    background: white;
    border-radius: 12px;
    padding: 32px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
}

/* ========== 서비스 제공 여부 ========== */
.service-toggle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 20px;
    background-color: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    margin-bottom: 20px;
}

.service-info {
    display: flex;
    align-items: center;
    gap: 12px;
}

.service-name {
    font-size: 16px;
    font-weight: 600;
    color: #212529;
}

.service-price {
    font-size: 14px;
    color: #6c757d;
}

/* ========== 토글 스위치 ========== */
.toggle-switch {
    display: flex;
    align-items: center;
    gap: 8px;
}

.toggle-switch input[type="radio"] {
    display: none;
}

.toggle-label {
    padding: 6px 16px;
    border: 1px solid #dee2e6;
    background-color: white;
    color: #6c757d;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.toggle-label:first-of-type {
    border-radius: 20px 0 0 20px;
    border-right: none;
}

.toggle-label:last-of-type {
    border-radius: 0 20px 20px 0;
}

.toggle-switch input[type="radio"]:checked + .toggle-label {
    background-color: #4852EA;
    color: white;
    border-color: #4852EA;
}

.toggle-switch input[type="radio"]:checked + .toggle-label + .toggle-label {
    border-left: 1px solid #dee2e6;
}

/* ========== 서비스 불가 안내 ========== */
.service-unavailable {
    padding: 20px;
    background-color: #fff3cd;
    border: 1px solid #ffeaa7;
    border-radius: 8px;
    margin-top: 20px;
}

.service-unavailable p {
    margin: 0;
    color: #856404;
    font-size: 14px;
    line-height: 1.5;
}

.service-unavailable a {
    color: #4852EA;
    font-weight: 600;
    text-decoration: none;
}

.service-unavailable a:hover {
    text-decoration: underline;
}

/* ========== 엔진오일 개선된 UI - 50대 친화적 대형 UI ========== */
.engine-oil-container {
    background: white;
    border-radius: 12px;
    padding: 32px;
}

/* 탭 네비게이션 - 더 크게 */
.oil-type-nav {
    display: flex;
    gap: 16px;
    margin-bottom: 32px;
}

.oil-type-nav-item {
    padding: 20px 40px;
    background-color: #f8f9fa;
    border: 2px solid #dee2e6;
    border-radius: 12px;
    font-size: 18px;
    font-weight: 600;
    color: #6c757d;
    cursor: pointer;
    transition: all 0.2s;
}

.oil-type-nav-item:hover {
    background-color: #e9ecef;
    border-color: #adb5bd;
}

.oil-type-nav-item.active {
    background-color: #4852EA;
    color: white;
    border-color: #4852EA;
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
}

/* 차종 선택 버튼 그룹 - 더 크게 */
.vehicle-type-selector {
    display: flex;
    gap: 16px;
    margin-bottom: 32px;
    flex-wrap: wrap;
}

.vehicle-type-btn {
    padding: 20px 32px;
    background-color: #f8f9fa;
    border: 2px solid #dee2e6;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 600;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 12px;
}

.vehicle-type-btn:hover {
    background-color: #e9ecef;
    border-color: #adb5bd;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.vehicle-type-btn.active {
    background-color: #4852EA;
    color: white;
    border-color: #4852EA;
    box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
}

.vehicle-type-icon {
    font-size: 28px;
}

/* 전체 선택 버튼 */
.master-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    background-color: #f8f9fa;
    border: 2px solid #e9ecef;
    border-radius: 12px;
    margin-bottom: 32px;
}

.master-control-label {
    font-size: 18px;
    font-weight: 700;
    color: #212529;
}

.select-all-btn {
    padding: 12px 32px;
    background-color: white;
    border: 2px solid #dee2e6;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    color: #495057;
    cursor: pointer;
    transition: all 0.2s;
}

.select-all-btn:hover {
    background-color: #f8f9fa;
    border-color: #adb5bd;
}

.select-all-btn.active {
    background-color: #4852EA;
    color: white;
    border-color: #4852EA;
}

/* 개선된 가격 테이블 - 훨씬 크게 */
.engine-oil-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    border: 2px solid #dee2e6;
    border-radius: 12px;
    overflow: hidden;
}

.engine-oil-table th {
    background-color: #f8f9fa;
    padding: 20px;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    color: #212529;
    border-bottom: 2px solid #dee2e6;
}

.engine-oil-table th:not(:last-child) {
    border-right: 1px solid #dee2e6;
}

.engine-oil-table td {
    padding: 20px;
    text-align: center;
    border-bottom: 1px solid #e9ecef;
    background-color: white;
}

.engine-oil-table td:not(:last-child) {
    border-right: 1px solid #e9ecef;
}

.engine-oil-table tbody tr:last-child td {
    border-bottom: none;
}

.engine-oil-table td:first-child {
    text-align: left;
    font-weight: 700;
    font-size: 16px;
    background-color: #f8f9fa;
}

/* 가격 셀 - 선택 가능한 카드 스타일 */
.price-cell {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 12px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    border: 2px solid transparent;
}

.price-cell:hover {
    background-color: #f8f9fa;
    transform: scale(1.05);
}

.price-cell.selected {
    background-color: #4852EA;
    color: white;
    border-color: #4852EA;
    box-shadow: 0 2px 8px rgba(72, 82, 234, 0.3);
}

/* 점도별 색상 구분 */
.price-cell.selected.viscosity-0w20 {
    background-color: #00b894;
    border-color: #00b894;
    box-shadow: 0 2px 8px rgba(0, 184, 148, 0.3);
}

.price-cell.selected.viscosity-5w30 {
    background-color: #4852EA;
    border-color: #4852EA;
    box-shadow: 0 2px 8px rgba(72, 82, 234, 0.3);
}

.price-amount {
    font-size: 18px;
    font-weight: 700;
}

.price-cell.selected .price-amount {
    color: white;
}

.viscosity-info {
    font-size: 12px;
    color: #6c757d;
    margin-top: 4px;
}

.price-cell.selected .viscosity-info {
    color: rgba(255, 255, 255, 0.8);
}

/* 서비스 불가 셀 */
.not-available-cell {
    color: #dee2e6;
    font-size: 24px;
    font-weight: 300;
}

/* 빈 상태 메시지 */
.empty-state {
    text-align: center;
    padding: 80px 20px;
    color: #6c757d;
}

.empty-state-icon {
    font-size: 64px;
    margin-bottom: 24px;
    opacity: 0.5;
}

.empty-state-text {
    font-size: 18px;
    line-height: 1.6;
}

/* 범례 */
.viscosity-legend {
    display: flex;
    gap: 24px;
    margin-top: 24px;
    padding: 16px;
    background-color: #f8f9fa;
    border-radius: 8px;
    justify-content: center;
    align-items: center;
}

.legend-item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    color: #495057;
}

.legend-color {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    border: 2px solid transparent;
}

.legend-color.viscosity-0w20 {
    background-color: #00b894;
}

.legend-color.viscosity-5w30 {
    background-color: #4852EA;
}

/* ========== 반응형 ========== */
@media (max-width: 768px) {
    .c-store-02 {
        padding: 16px;
    }
    
    .carcenter-content {
        flex-direction: column;
    }
    
    .service-sidebar {
        width: 100%;
        position: static;
    }
    
    .inspection-content,
    .service-main-content {
        padding: 20px;
    }
    
    .price-table {
        font-size: 12px;
        overflow-x: auto;
        display: block;
    }
    
    /* 엔진오일 모바일 대응 */
    .oil-type-nav-item {
        padding: 16px 24px;
        font-size: 16px;
    }
    
    .vehicle-type-btn {
        padding: 16px 20px;
        font-size: 14px;
    }
    
    .vehicle-type-icon {
        font-size: 24px;
    }
    
    .engine-oil-table {
        font-size: 12px;
        overflow-x: auto;
        display: block;
    }
    
    .engine-oil-table th,
    .engine-oil-table td {
        padding: 12px;
    }
    
    .price-amount {
        font-size: 14px;
    }
    
    .master-control {
        flex-direction: column;
        gap: 16px;
        text-align: center;
    }
    
    .master-control-label {
        font-size: 16px;
    }
}

/* 반응형 - 태블릿 이상에서도 큰 사이즈 유지 */
@media (max-width: 1200px) {
    .engine-oil-table {
        font-size: 14px;
    }
    
    .price-amount {
        font-size: 16px;
    }
}